// 纠正专有名词
export class ProperNounsCorrector {
  public static rules = ['properNouns'];

  public static handle(text: string): string {

    for (const i in properNouns) {
      if (properNouns.hasOwnProperty(i)) {
        text = text.replace(
          /\w+/g,
          $ => $.match(new RegExp(`^${i}$`, 'i')) ? properNouns[i] : $
        );
      }
    }

    return text;
  }
}

export default ProperNounsCorrector;
